<template id="">
<div class="pics">
  <div class="commen-head">热门赛事</div>
  <!--因为图片要横向滚动，那么父级就要设定一个固定宽度-->
  <!--当子元素的宽度超过父元素的宽度的时候，就可以滚动了-->
  <div class="pic-wrapper" ref="picWrapper">
    <ul class="pic-list" ref="picList">
      <li class="pic-item" v-for="item in lists">
        <img :src="item.thumbnail[0]" width="240" height="130">
        <p class="name">{{item.title}}</p>
      </li>
    </ul>
  </div>
</div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data() {
    return {
      lists: []
    }
  },
  created() {
    this.getLists();
  },
  mounted() {
    //等到內容將div撑开之后，在对better-scroll的实例初始化
    //初始化的时机很重要
    this.$nextTick(() => {
      this._initPics(); //商家实景的横向滚动
    });
  },
  watch: {
    //监听seller变量的值的变化在初始化
    //防止滚动不生效
    'seller'() {
      this.$nextTick(() => {
        this._initPics();
      });
    }
  },
  methods: {
    getLists() {
      this.axios.get(this.$store.state.domain + "api/game/list").then(data => {
        console.log(data.data.data.data)
        this.lists = data.data.data.data
      })
    },
    //用于初始化“商家实景”的横向滚动效果
    _initPics() {
      //先判断是否有这个属性
      if (this.lists) {
        //这里之所以要设置宽度，是因为.pic-wrapper和.pic-list的宽度一样大
        //当.pic-list的宽度大于.pic-wrapper的宽度，才能横向滚动
        let picWidth = 240; //这里是设置每张图片的宽度
        let margin = 6;
        //width是所有图片的宽度和以及margin的大小和
        let width = (picWidth + margin) * this.lists.length - margin;
        console.log(width);
        this.$refs.picList.style.width = width + 'px'; //设置.pic-list的宽度的宽度
        this.$nextTick(() => {
          if (!this.picScroll) {
            this.picScroll = new BScroll(this.$refs.picWrapper, {
              scrollX: true,
              eventPassthrough: 'vertical' //忽略竖直方向的滚动
            });
          } else {
            this.picScroll.refresh();
          }
        });
      }

    }
  }
}
</script>
<style scoped>
.pics {
  padding: 18px;
}

.name {
  font-size: 14px;
  line-height: 30px;
  height: 30px;
}

.title {
  margin-bottom: 12px;
  line-height: 14px;
  color: rgb(7, 17, 27);
  font-size: 14px;
}

.pic-wrapper {
  width: 100%;
  overflow: hidden;
  white-space: nowrap;
  margin-top: 10px;
  height: 160px;
}

.pic-list {
  font-size: 0;
}

.pic-item {
  display: inline-block;
  margin-right: 6px;
  width: 240px;
  height: 160px;
  &:last-child;
}
</style>



